import React from 'react'
import { Section } from '../components'
// eslint-disable-next-line
import { Row, Col, Button, Form } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'



export class Contact extends React.Component {
  render () {
    return (
      <Section id="contact" title="Get In Touch">
        <Row>
          <Col md={4}>
          
              
              <Row>
                <FontAwesomeIcon icon={['fas', 'mobile-alt']} color="#ff4c60" size="2x" style={{marginTop:"20px", marginLeft:"10px"}}/>
                <div className="details" style={{marginLeft:"40px"}}>
                  
                  <h5>mobile</h5>
                  <p>+20 1097031936</p>
                  <p>+20 1555004533</p>
                </div>
					  </Row>
            <Row>
          
          <FontAwesomeIcon icon={['fab', 'whatsapp']} color="#ff4c60" size="2x" style={{marginTop:"20px", marginLeft:"10px"}}/>
         <div className="details" style={{marginLeft:"40px"}}>
           <h5>Whatsapp</h5>
           <p><a className="whlink" href="https://wa.me/201097031936">https://wa.me/201097031936</a></p>
         </div>
       </Row>


				
          
           


          </Col>
          <Col md={4}>
          <Row>
          
						 <FontAwesomeIcon icon={['fab', 'whatsapp']} color="#ff4c60" size="2x" style={{marginTop:"20px", marginLeft:"10px"}}/>
						<div className="details" style={{marginLeft:"40px"}}>
							<h5>Scan Whatsapp </h5>
							<img src="images/whatsapp.jpg" alt="" width="150px" />
						</div>
					</Row>
          
         
          </Col>
          <Col md={4}>
          <Row>
						 <FontAwesomeIcon icon={['fas', 'envelope-square']} color="#ff4c60" size="2x" style={{marginTop:"20px", marginLeft:"10px"}}/>
						<div className="details" style={{marginLeft:"40px"}}>
							<h5>Email address</h5>
             	<span>prog.basma@gmail.com</span>
						</div>
					</Row>
          

          <Row>
						 <FontAwesomeIcon icon={['fas', 'map-marker-alt']} color="#ff4c60" size="2x" style={{marginTop:"20px", marginLeft:"10px"}}/>
						<div className="details" style={{marginLeft:"40px"}}>
							<h5>Location</h5>
							<span>Mansoura , Egypt</span>
						</div>
					</Row>
          </Col>


        </Row>
      </Section>
    )
  }
}

export default Contact
